.header {
  scroll-behavior: smooth;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  display: flex;
}

.header__nav {
  align-items: center;
  font-weight: 500;
  display: flex;
}

.header__item {
  margin-left: 20px;
}

.header__item:first-child {
  margin-left: 0;
}

.header__link {
  color: #060b35;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.header__link:hover {
  color: #00acdc;
}

.header__list {
  width: 100%;
  list-style: none;
  display: flex;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  color: #00acdc;
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

html {
  font-family: Roboto, sans-serif;
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 200px;
  height: 408px;
  transition: all .3s;
  display: flex;
}

.card:hover {
  border: 1px solid #f3f3f3;
  transform: scale(1.2);
}

.card__image {
  border: none;
  width: 160px;
  height: 134px;
  margin: 32px 0 0;
  padding: 0;
  display: block;
}

.card__profuctName {
  color: #060b35;
  width: 166px;
  height: 36px;
  margin: 40px 0 0;
  padding: 0;
  font-family: Roboto-medium, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  display: block;
}

.card__productCode {
  color: #616070;
  width: 166px;
  margin: 4px 0 0;
  font-family: Roboto-Regular, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__stars {
  justify-content: space-between;
  width: 166px;
  margin-top: 16px;
  line-height: 16px;
  display: flex;
}

.card__reviews {
  text-align: right;
  color: #060b35;
  float: right;
  font-family: Roboto-Regular, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 16px;
}

.card__reviews p {
  text-align: right;
  float: right;
}

.card__price {
  justify-content: space-between;
  width: 166px;
  margin-top: 24px;
  display: flex;
}

.card__label {
  text-align: left;
  color: #616070;
  font-family: Roboto-Regular, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__money {
  color: #060b35;
  font-family: Roboto-Bold, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy {
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin: 16px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  transition: all .3s;
}

.card__buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  margin: 0;
  padding: 0;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin: 0;
  padding: 0;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
}

.catalog {
  grid-template-columns: repeat(1, 200px);
  place-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

.catalog--card :hover .card__profuctName {
  color: #34568b;
}

@font-face {
  font-family: Roboto-Regular;
  src: url("Roboto-Regular.ca197847.ttf");
}

@font-face {
  font-family: Roboto-Bold;
  src: url("Roboto-Bold.fdb9b54a.ttf");
}

@font-face {
  font-family: Roboto-medium;
  src: url("Roboto-Medium.91322885.ttf");
}

@font-face {
  font-family: Roboto;
  src: url("Roboto-Light.1695f332.ttf");
}

body {
  margin: 0;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}
